import {WanderScene} from "../../rpg-engine/wander-scene";
import {Type} from "@angular/core";
import {JourneyDeparture} from "../../rpg-engine/scenes/journey-departure";
import {WorldMapViewComponent} from "./world-map-view/world-map-view.component";
import {Journey} from "../../rpg-engine/scenes/journey";
import {JourneyStepComponent} from "./journey-step/journey-step.component";
import {TownNav} from "../../rpg-engine/scenes/town-nav";
import {TownUIComponent} from "./town-ui/town-ui.component";
import {Roaming} from "../../rpg-engine/scenes/roaming";
import {RoamingStepComponent} from "./roaming-step/roaming-step.component";
import {PassageTrail} from "../../rpg-engine/scenes/passage-trail";
import {PassageThroughComponent} from "./passage-through/passage-through.component";
import {Narration} from "../../rpg-engine/scenes/narration";
import {NarrationPagesComponent} from "./narration-pages/narration-pages.component";
import {SceneRenderer} from "./scene-renderer";
import {StoryBranch} from "../../rpg-engine/scenes/story-branch";
import {StoryChoiceComponent} from "./story-choice/story-choice.component";
import {SkillActionComponent} from "./skill-action/skill-action.component";
import {SkillTest} from "../../rpg-engine/scenes/skill-test";

export class SceneRendererLoader
{
		static getSceneRendererClass (scene: WanderScene): Type<SceneRenderer>
	{
		let renderer;
		if (scene instanceof JourneyDeparture)
		{
			renderer = WorldMapViewComponent;
		}
		else if (scene instanceof Journey)
		{
			renderer = JourneyStepComponent;
		}
		else if (scene instanceof TownNav)
		{
			renderer = TownUIComponent;
		}
		else if (scene instanceof Roaming)
		{
			renderer = RoamingStepComponent;
		}
		else if (scene instanceof PassageTrail)
		{
			renderer = PassageThroughComponent;
		}
		else if (scene instanceof Narration)
		{
			renderer = NarrationPagesComponent;
		}
		else if (scene instanceof StoryBranch)
		{
			renderer = StoryChoiceComponent;
		}
		else if (scene instanceof SkillTest)
		{
			renderer = SkillActionComponent;
		}

		return renderer;
	}
}
