import {HttpClient} from "@angular/common/http";
import {WorldMap} from "./world/world-map";
import {WanderHero} from "./wander-hero";
import {WanderScene} from "./wander-scene";
import {QuestRef} from "./quests/quest-ref";
import {ResLoader} from "../support/res-loader";
import {SkillSystem} from "./skill-system/skill-system";
import {SimpleSkillSystem} from "./skill-system/simple-skill-system";
import {MetaScene} from "./scenes/meta-scene";

export class WanderGame {

	activescene: WanderScene;
	hero: WanderHero;
	worldmap: WorldMap;
	worldmeta: object;
	// TODO: load quest data on demand and just use triggers
	quests: Map<string, QuestRef>;

	skillsystem: SkillSystem;

	constructor(private loader: ResLoader)
	{
		this.quests = new Map<string, QuestRef>();
	}

	static loadGame (http: HttpClient, callback: (game: WanderGame) => void): void
	{
		const loader = new GameFactory(http);
		loader.loadDemoGame(function (game: WanderGame, factory: GameFactory)
		{
			console.log("loaded game ", game);
			console.log("built from factory ", factory);
			callback(game);
		});

	}

	// DEBUG FUNCTIONS FOR QUICK TESTING

	setHero (hero: WanderHero)
	{
		this.hero = hero;
	}

	setWorldMap (worldmap: WorldMap)
	{
		this.worldmap = worldmap;
	}

	setWorldMeta (src: object)
	{
		// TODO: placeholder
		this.worldmeta = src;
	}

	setSkillSystem (skillsys: SkillSystem)
	{
		this.skillsystem = skillsys;
	}

	setActiveScene (scene: WanderScene)
	{
		console.log("setting activescene to ", scene);

		if (scene instanceof MetaScene)
		{
			console.log("should switch to internal scene for metascene");
			this.activescene = scene.getCurrent();
		}
		else
		{
			this.activescene = scene;
		}

		console.log("ACTIVESCENE:", this.activescene);
	}

	getActiveScene (): WanderScene
	{
		return this.activescene;
	}

	addQuest (qref: QuestRef)
	{
		this.quests.set(qref.questid, qref);
	}


}

class GameFactory {

	dataset: object;
	loader: ResLoader;

	constructor(protected http: HttpClient) {
		this.dataset = {
			"locs": null,
			"terrain": null,
			"player": null,
			"quests": null,
			"worldmeta": null,
			"skillsys": null
		};

		this.loader = new ResLoader(this.http);
	}





	loadDemoGame (callback: Function) {

		const sources = {
			"locs": "./assets/gamedata/testlocations.json",
			"terrain": "./assets/gamedata/testmapgame.json",
			"player": "./assets/gamedata/playerdata.json",
			"worldmeta": "./assets/gamedata/worlddesign.json",
			"skillsys": "./assets/gamedata/simpleskillactions.json"
		};

		let loader: GameFactory = this;

		function getCheckerFunction(key: string): Function {
			return function (data: any) {
				loader.setData(key, data, callback);
			};
		}

		for (let key of Object.keys(sources)) {
			let path = sources[key];
			let checker = getCheckerFunction(key);
			this.loader.loadDataFile(path, checker);
		}

		let QPATH = "./assets/gamedata/quests/";
		let QUESTS = [
			QPATH + "drunkquest.json",
			QPATH + "sidestreetquest.json",
			// QPATH + "simpleskillactions.json",
			QPATH + "testquest.json",
			QPATH + "triggerquest.json"
		];

		this.loader.loadDataFiles(QUESTS, function (quests: object) {
			loader.setData("quests", quests, callback);
		});

	}

	setData(key: string, data: any, completer: Function): void {
		this.dataset[key] = data;

		let has_all = true;
		for (let datakey of Object.keys(this.dataset)) {
			if (this.dataset[datakey] == null) {
				has_all = false;
			}
		}

		if (has_all) {
			// completer(this);
			this.createGameInstance(completer);
		}
	}

	createGameInstance(completer: Function): void {
		let game = new WanderGame(new ResLoader(this.http));

		console.log("loaded terrain", this.dataset["terrain"]);
		console.log("loaded locs", this.dataset["locs"]);
		console.log("loaded player", this.dataset["player"]);
		console.log("loaded quests: ", this.dataset["quests"]);


		let worldmap = WorldMap.fromJSON(this.dataset["terrain"], this.dataset["locs"]);
		let hero = WanderHero.fromJSON(this.dataset["player"]);
		let skillsys = SimpleSkillSystem.fromJSON(this.dataset["skillsys"]);
		game.setWorldMap(worldmap);
		game.setHero(hero);
		game.setWorldMeta(this.dataset["worldmeta"]);
		game.setSkillSystem(skillsys);

		for (let questfile in this.dataset["quests"])
		{
			let qref = QuestRef.fromJSON(this.dataset["quests"][questfile], questfile);
			console.log("created from ", questfile, "questref ", qref);
			// let quest = Quest.fromJSON(this.dataset["quests"][questfile]);
			// console.log("created from ", questfile, " quest object ", quest);
			game.addQuest(qref);
		}


		completer(game, this);

	}



}
