import {Observable, Subject} from "rxjs";
import {WanderGame} from "../rpg-engine/wander-game";
import {WanderScene} from "../rpg-engine/wander-scene";

export class SceneRef {

	private readonly _afterClosed = new Subject<any>();
	afterClosed: Observable<any> = this._afterClosed.asObservable();

	private readonly _sceneCheck = new Subject<any>();
	sceneCheck: Observable<any> = this._sceneCheck.asObservable();

	private readonly _subSwitch = new Subject<WanderScene>();
	subSwitch: Observable<any> = this._subSwitch.asObservable();
	
	scene: WanderScene;
	game: WanderGame;

	constructor() {
	}

	setContext (scene: WanderScene)
	{
		this.scene = scene;
		this.game = scene.game;
	}

	close(result?: any) {
		this._afterClosed.next(result);
	}

	triggerSceneCheck ()
	{
		console.log("sending check event for scene ", this.scene, this.scene.sceneFinished());
		this._sceneCheck.next();
	}

	triggerSubScene (scene: WanderScene)
	{
		this._subSwitch.next(scene);
	}


}
