import {WanderScene} from "../wander-scene";
import {WanderGame} from "../wander-game";

export class Narration extends WanderScene
{

	images: string[];
	paragraphs: string[];
	title: string;

	progress: number;

	// keep back button visible so the user can re-read stuff
	allowReadBack: boolean;
	// keep the title visible on all sub-pages rather than on the first only
	keepTitleOn: boolean;

	constructor (game: WanderGame, title: string, paragraphs: string[], images: string[] = [])
	{
		super(game);

		this.progress = 0;
		this.allowReadBack = true;
		this.keepTitleOn = true;

		this.paragraphs = paragraphs;
		this.images = images;
		this.title = title;
	}

	sceneFinished(): boolean {
		return this.progress >= this.paragraphs.length - 1;
	}

	advanceNarration ()
	{
		this.progress =  Math.min(this.progress + 1, this.paragraphs.length - 1);
	}

	backtrackNarration ()
	{
		this.progress = Math.max(this.progress - 1, 0);
	}

	getCurrent (): string
	{
		return this.paragraphs[this.progress];
	}

}
