import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {StoryTesterComponent} from "./story-tester/story-tester.component";
import {StorytellerComponent} from "./storyteller/storyteller.component";


const routes: Routes = [
	{path: '', redirectTo: '/game', pathMatch: 'full'},
	{path: 'tester', component: StoryTesterComponent},
	{path: 'game', component: StorytellerComponent},
	
];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule {

}
