export class WorldPosition
{

	readonly x: number;
	readonly y: number;

	constructor (x: number, y: number)
	{
		this.x = x;
		this.y = y;
	}

	static fromCoordsArray (coords: number[]): WorldPosition
	{
		return new WorldPosition(coords[0], coords[1]);
	}

	public getCoords (): number[]
	{
		return [this.x, this.y];
	}

	public getShifted (x: number, y: number)
	{
		return new WorldPosition(this.x + x, this.y + y);
	}

	public matches (other: WorldPosition)
	{
		// console.log("checking if we have a match between ", this, "and", other);
		return this.x === other.x && this.y === other.y;
	}

	static getRelativeGeneralDirection (pfrom: WorldPosition, pto: WorldPosition): number[]
	{
		const distX = pto.x - pfrom.x;
		const distY = pto.y - pfrom.y;

		let dirX = 0;
		let dirY = 0;
		if (distX === 0)
		{
			dirX = 0;
			dirY = distY > 0 ? 1 : -1;
		}
		else if (distY === 0)
		{
			dirY = 0;
			dirX = distX > 0 ? 1 : -1;
		}
		else
		{
			let absX = Math.abs(distX);
			let absY = Math.abs(distY);
			if (absY > 3 * absY)
			{
				dirX = 0;
				dirY = distY > 0 ? 1 : -1;
			}
			else if (absY * 3 < absX) {
				dirX = distX > 0 ? 1 : -1;
				dirY = 0;
			}
			else {
				dirX = distX > 0 ? 1 : -1;
				dirY = distY > 0 ? 1 : -1;
			}
		}

		return [dirX, dirY];
	}
}
