import {Component, OnInit, AfterViewInit} from '@angular/core';
import {Narration} from '../../../rpg-engine/scenes/narration';
import {SceneRef} from '../../../scene-ui/scene-ref';
import {SceneRenderer} from '../scene-renderer';

@Component({
	selector: 'app-narration-pages',
	templateUrl: './narration-pages.component.html',
	styleUrls: ['./narration-pages.component.scss']
})
export class NarrationPagesComponent extends SceneRenderer implements OnInit, AfterViewInit {

	tale: Narration;
	lastpage: boolean;

	constructor(public sceneref: SceneRef) {
		super(sceneref);
		this.tale = this.sceneref.scene as Narration;
		console.log("tale info", this.tale);
	}

	ngOnInit() {
	}

	ngAfterViewInit() {

	}

	readForward ()
	{
		this.tale.advanceNarration();
		this.lastpage = this.tale.sceneFinished();

	}

	readBackwards ()
	{
		this.tale.backtrackNarration();
	}

	readOut ()
	{
		if (this.tale.sceneFinished())
		{
			this.sceneref.triggerSceneCheck();
		}
	}
}
