import {WanderScene} from "../wander-scene";
import {WanderGame} from "../wander-game";

export class StoryBranch extends WanderScene
{
	/**
	 * Option selection without specific conditions, returns the chosen option
	 */

	// simple selection of options without specific conditions, returns the choice
	// TODO: add subclass with conditions connected to the display of each choice

	// maps an optionid to the leave state of the scene
	// (it is technically possible to have the same leave state for several options,
	// plus 0 cannot be used since it's generic scene finished value so a straight association would not work)

	options: ChoiceOption[];

	// options: Map<number, number>;
	// descriptions: Map<number, string>;
	// notes: Map<number, string>;

	private selected: ChoiceOption;

	image: string;
	prompt: string;



	constructor (game: WanderGame)
	{
		// a skill test is a scene in which the player is given a set of possible skill throws, each with its own return code
		super(game);
		this.prompt = null;
		this.options = [];
		// this.options = new Map<number, number>();
		// this.descriptions = new Map<number, string>();
		// this.notes = new Map<number, string>();
		this.selected = null;
		this.image = null;
	}

	sceneFinished(): boolean {
		return this.returnState() !== WanderScene.LEAVESTATE_UNFINISHED;
	}

	setImage (filename: string)
	{
		this.image = filename;
	}

	addOption (desc: string, endstate: number = 0, note: string = null)
	{
		let nextcode = this.options.length + 1;
		this.options.push({
			optionid: nextcode,
			leavestate: endstate,
			description: desc,
			notes: note
		});

	}

	setSelected (opt: ChoiceOption)
	{
		this.selected = opt;
	}

	returnState (): number
	{
		return !!this.selected ? this.selected.leavestate : -1;
	}


}



export interface ChoiceOption {

	optionid: number;
	leavestate: number;
	description: string;
	notes: string;

}
