export class SimpleSkillSet
{

	potentials: Map<string, number>;
	developments: Map<string, number>;

	constructor ()
	{
		this.potentials = new Map<string, number>();
		this.developments = new Map<string, number>();
	}


	registerPotential (id: string, basevalue: number)
	{
		this.potentials.set(id, basevalue);
	}

	registerDevelopment (id: string, basevalue: number)
	{
		this.developments.set(id, basevalue);
	}

	public getPotential (id: string)
	{
		return this.potentials.has(id) ? this.potentials.get(id) : 0;
	}

	public getDevelopment (id: string)
	{
		return this.developments.has(id) ? this.developments.get(id) : 0;
	}



}

