import {Component, OnInit} from '@angular/core';
import {StorytellerComponent} from "../../storyteller.component";
import {StorytellerInterface} from "../../storyteller-interface";
import {WanderGame} from "../../../rpg-engine/wander-game";

@Component({
	selector: 'app-quest-journal',
	templateUrl: './quest-journal.component.html',
	styleUrls: ['./quest-journal.component.scss']
})
export class QuestJournalComponent implements OnInit {

	controller: StorytellerComponent;
	game: WanderGame;

	constructor(storytellerinterface: StorytellerInterface = null) {
		console.warn("creating charsheet w controller interface", storytellerinterface);
		this.controller = storytellerinterface.getStoryteller();
		this.game = this.controller.game;
	}

	ngOnInit() {
		console.warn("showing charsheet w controller", this.controller);
	}

}
