import {SimpleSkillThrow} from "./simple-skill-throw";
import {AgentEntity} from "./agent-entity";
import {SimpleSkillSet} from "./simple-skill-set";

export class SimpleSkillSystem
{

	// basic skill system just for testing purposes
	potentials: string[];
	actions: Map<string, SimpleSkillThrow>;

	constructor ()
	{
		this.potentials = [];
		this.actions = new Map<string, SimpleSkillThrow>();
	}

	static fromJSON (src: object): SimpleSkillSystem
	{
		let created = new SimpleSkillSystem();
		created.potentials = src["potentials"];
		for (let actionid in src["actions"])
		{
			created.actions.set(actionid, SimpleSkillThrow.fromJson(src["actions"][actionid]));
		}
		return created;
	}

	static getLowestBalanced (series: number[]): number
	{
		let lowest = series[0];
		for (let current of series)
		{
			lowest = Math.min(lowest, current);
		}

		let numlowest = 0;
		for (let current of series)
		{
			if (current === lowest)
			{
				numlowest++;
			}
		}

		if (numlowest<2)
		{
			return numlowest+1;
		}
		else
		{
			return numlowest;
		}
	}

	static getHighest (series: number[]): number
	{
		let highest = series[0];
		for (let current of series)
		{
			highest = Math.max(highest, current);
		}
		return highest;
	}

	applySkill (actionid: string, agent: AgentEntity)
	{
		const agentskills = agent.getSkills();
		const action = this.actions.get(actionid);
		let pot_vals = [];
		for (let potential of action.potentials)
		{
			pot_vals.push(agentskills.getPotential(potential));
		}

		let dev_vals = [];
		for (let development of action.developments)
		{
			dev_vals.push(agentskills.getDevelopment(development));
		}

		let potval = pot_vals.length > 1 ? SimpleSkillSystem.getLowestBalanced(pot_vals) : pot_vals[0];
		let devval = dev_vals.length > 1 ? SimpleSkillSystem.getHighest(dev_vals) : dev_vals[0];

		let dicethrow = 0;
		for (let i = 0; i < devval; i++)
		{
			dicethrow += Math.floor(Math.random()*potval)+1;
		}

		return dicethrow;
	}



}
