import {Component, OnInit} from '@angular/core';
import {SceneUIService} from "../scene-ui/scene-ui.service";
import {TestBlueScreenComponent} from "./scenes/test-blue-screen/test-blue-screen.component";
import {TestGreenScreenComponent} from "./scenes/test-green-screen/test-green-screen.component";
import {TesterScene} from "./tester-scene";
import {StorytellerInterface} from "../storyteller/storyteller-interface";

@Component({
	selector: 'app-story-tester',
	templateUrl: './story-tester.component.html',
	styleUrls: ['./story-tester.component.scss']
})
export class StoryTesterComponent implements OnInit {


	constructor(private sceneui: SceneUIService, sti: StorytellerInterface = null) {
	}

	ngOnInit() {
	}

	loadBlueScreen() {
		let scene = new TesterScene();
		let ref = this.sceneui.open(TestBlueScreenComponent, scene);
		ref.afterClosed.subscribe(result => {
			console.log('Scene (BLUE) closed', result);
		});
	}

	loadGreenScreen() {
		let scene = new TesterScene();
		let ref = this.sceneui.open(TestGreenScreenComponent, scene);
		ref.afterClosed.subscribe(result => {
			console.log('Scene (GREEN) closed', result);
		});
	}
}
