import {AfterViewInit, Component, OnInit} from '@angular/core';
import {SceneRenderer} from "../scene-renderer";
import {SceneRef} from "../../../scene-ui/scene-ref";
import {PassageTrail} from "../../../rpg-engine/scenes/passage-trail";

@Component({
	selector: 'app-passage-through',
	templateUrl: './passage-through.component.html',
	styleUrls: ['./passage-through.component.scss']
})
export class PassageThroughComponent  extends SceneRenderer implements OnInit, AfterViewInit {

	trail: PassageTrail;
	direction: number;

	constructor(public sceneref: SceneRef) {
		super(sceneref);
		this.trail = this.sceneref.scene as PassageTrail;
	}

	isTrailing ()
	{
		return this.trail.progress > -1 && this.trail.progress < this.trail.passage.length();
	}

	journeyOnwards ()
	{
		this.trail.step();
	}

	leaveTrail ()
	{
		this.trail.leaving = true;
		this.sceneref.triggerSceneCheck();
	}

	ngOnInit() {
	}

	ngAfterViewInit(): void {
	}

}
