import {AfterViewInit, Component, OnInit} from '@angular/core';
import {SceneRenderer} from "../scene-renderer";
import {SceneRef} from "../../../scene-ui/scene-ref";
import {StorytellerInterface} from "../../storyteller-interface";
import {ChoiceOption, StoryBranch} from "../../../rpg-engine/scenes/story-branch";

@Component({
  selector: 'app-story-choice',
  templateUrl: './story-choice.component.html',
  styleUrls: ['./story-choice.component.scss']
})
export class StoryChoiceComponent extends SceneRenderer implements OnInit, AfterViewInit  {

	choice: StoryBranch;

	constructor(public sceneref: SceneRef, storytellerinterface: StorytellerInterface = null) {

		super(sceneref, storytellerinterface);
		console.log("StoryChoice injected w stinterface: ", storytellerinterface);
		this.choice = this.sceneref.scene as StoryBranch;
		console.log("current choice set is ", this.choice);
	}

	ngOnInit() {
	}

	ngAfterViewInit(): void {
	}

	selectOption (opt: ChoiceOption)
	{
		this.choice.setSelected(opt);
		this.sceneref.triggerSceneCheck();
	}

}
