import {Town} from "./Town";
import {TerrainPass} from "./TerrainPass";
import {Place} from "./Place";

export class PlaceFactory
{
	static CLASSES = {
		"towns": Town,
		"passes": TerrainPass
	};

	static fromJSON (placetype: string, placeid: string, src: object): Place
	{
		let builderclass = PlaceFactory.CLASSES[placetype];
		return builderclass.fromJSON(placeid, src);
	}
}
