import {WanderScene} from "../wander-scene";
import {Quest} from "./quest";
import { SceneFactory } from '../scene-factory';
import {QuestRef} from "./quest-ref";

export class QuestTask
{

	// a quest task is a container for a scene object that also includes a condition and a number of other items (TBD) such as journal updates etc.

	taskid: string;
	scene: WanderScene;
	journal_after: string;
	prompt: string;

	// we use this to make the conditions check quicker, a completed  task
	// TODO: consider removing the task from the quest altogether after completion
	// but for now we'll keep a flag on it
	completed: boolean;

	// by default a questtask never triggers anything
	caller: QuestRef;

	// by default a questtask never triggers anything
	triggered: string;

	constructor (taskid: string, scene: WanderScene, prompt: string, journal_after: string)
	{
		this.taskid = taskid;
		this.scene = scene;
		this.journal_after = journal_after;
		this.prompt = prompt;
		this.completed = false;
		this.caller = null;
		this.triggered = null;
	}

	getReturnStates (): Map<string, number>
	{
		let states = new Map<string, number>();

		// TODO: handle multiscene per Java example
		/*
		if (this.scene instanceof MultiScene)
		{
			// this should flatten the hierarchy under it
			Map<String, Integer> scenestates = ((MultiScene)this.scene).returnStates();
			for (String sceneid: scenestates.keySet())
			{
				String sceneidcombo = this.taskid+"."+sceneid;
				states.put(sceneidcombo, scenestates.get(sceneid));
			}
		}
		else
		{
			states.put(this.taskid, this.scene.returnState());
		}
		*/

		states.set(this.taskid, this.scene.returnState());
		return states;
	}

	setCaller (questref: QuestRef)
	{
		this.caller = questref;
	}

	isCompleted (): boolean
	{
		return this.completed;
	}

	setCompleted ()
	{
		this.completed = true;
		if (this.caller != null)
		{
			this.caller.setCompletedTask(this);
		}
	}

	setTriggered (taskid: string)
	{
		this.triggered = taskid;
	}

	static fromJSON (src: object): QuestTask
	{

		// NOTE: we may want to generate the scene ON THE FLY later

		let taskid = src["taskid"];
		let scene = SceneFactory.fromJSON(src["scene"]);

		let prompt = src["prompt"];
		let journal_after = !!src["journal_after"] ? src["journal_after"] : null;

		let task = new QuestTask (taskid, scene, prompt, journal_after);
		return task;
	}

}
