import {WanderScene} from "../wander-scene";
import {WanderGame} from "../wander-game";
import {SkillSystem} from "../skill-system/skill-system";
import {AgentEntity} from "../skill-system/agent-entity";

export class SkillTest extends WanderScene
{
	// keeping this as maps in case we need a more complex handling in the future, though basically it's an arraylist
	options: SkillTestOption[];

	// whether the player can leave the scene
	canleave: boolean;
	situation: string;
	success: number;

	// played is different from success != 0 ince we have the condition to leave
	played: boolean;

	image: string;


	constructor (game: WanderGame)
	{
		super(game);
		// a skill test is a scene in which the player is given a set of possible skill throws, each with its own return code

		this.options = [];
		this.canleave = true;
		this.success = 0;
		this.played = false;
		this.image = null;
	}

	sceneFinished(): boolean
	{
		console.log("checking scene finished for SkillTest", this.played);
		return this.played;
	}

	setImage (filename: string)
	{
		this.image = filename;
	}

	returnState (): number
	{
		// 0 is not finished/played yet
		// all alternatives are X for success, -X for failure
		// TODO: add other standard return codes (plus handling for custom results)
		// also see the addAction todo note

		// NOTE: Leaving the skilltest without trying (when allowed by the scene design)
		// implies getting a status of 0 and a sceneFinished = True
		// TODO: find a way to set this
		return this.success;
	}


	setDesc (situation: string)
	{
		this.situation = situation;
	}

	setCanLeave (allow: boolean)
	{
		this.canleave = allow;
	}

	addAction (option: SkillTestOption)
	{
		this.options.push(option);
	}


	playActionDebug (option: SkillTestOption, succeed: boolean): boolean
	{
		// ONLY for debug, dev can force success or fail while we develop and balance the skill system

		if (succeed)
		{
			this.success = option.returncode;
			this.played = true;
			return true;
		}
		else
		{
			// 0 is infinitely repeatable
			if (!option.isInfinitelyRepeatable())
			{
				option.addTry();
				if (!option.isStillRepeatable())
				{
					this.success = -option.returncode;
					this.played = true;
				}
			}
			return false;
		}

	}

	playAction (option: SkillTestOption, rules: SkillSystem, user: AgentEntity): boolean
	{

		// TODO: get SkillSystem and AgentEntity from game

		const actionid = option.skillactionid;
		const target = option.target;

		const performance = rules.applySkill(actionid, user);
		if (performance >= target)
		{
			this.success = option.returncode;
			this.played = true;
			return true;
		}
		else
		{
			// 0 is infinitely repeatable
			if (!option.isInfinitelyRepeatable())
			{
				option.addTry();
				if (!option.isStillRepeatable())
				{
					this.success = -option.returncode;
					this.played = true;
				}
			}
			return false;
		}

	}

	leaveAction ()
	{
		this.played = true;
		// redundant, for clarity
		this.success = 0;
	}




}

export class SkillTestOption
{
	skillactionid: string;
	description: string;
	story_failure: string;
	story_success: string;

	max_tries: number;
	target: number;
	returncode: number;
	tried: number;

	constructor (skillactionid: string, target: number)
	{
		this.skillactionid = skillactionid;
		this.target = target;
		// default is infinite retries
		this.max_tries = 0;
		this.tried = 0;
	}


	addTry ()
	{
		this.tried++;
	}

	isStillRepeatable(): boolean
	{
		return this.max_tries != 0 || this.tried < this.max_tries;
	}

	isRepeatable(): boolean
	{
		return this.max_tries != 1;
	}

	isInfinitelyRepeatable(): boolean
	{
		return this.max_tries == 0;
	}

	setRepeatable (max_tries: number)
	{
		this.max_tries = max_tries;
	}

	setText (description: string, success: string, failure: string)
	{
		this.description = description;
		this.story_success = success;
		this.story_failure = failure;
	}

	getFeedbackSuccess (): string
	{
		if (this.story_success != null)
		{
			return this.story_success;
		}
		else
		{
			return "You succeed";
		}
	}

	getFeedbackFailure (): string
	{
		if (this.story_failure != null)
		{
			return this.story_failure;
		}
		else
		{
			return "You failed";
		}
	}

	getDescription (): string
	{
		if (this.description != null)
		{
			return this.description;
		}
		else
		{
			return "Try your "+this.skillactionid+" ability";
		}
	}

	static fromJSON (src: object):  SkillTestOption
	{
		const actionid = src["action"];
		const target = src["target"];
		let created = new SkillTestOption(actionid, target);
		if (src.hasOwnProperty("repeatable"))
		{
			created.setRepeatable(src["repeat"]);
		}

		let description = !!src["prompt"] ? src["prompt"] : "Use your ability at "+actionid;
		let success = !!src["success"] ? src["successs"] : "Succeded in "+actionid;
		let failure = !!src["failure"] ? src["failure"] : "Failed at "+actionid;

		created.setText(description, success, failure);

		// note that the match does not get LEAVESTATE_FINISHED as a value, but falls back to it anyway
		created.returncode = !!src["returncode"] ? src["returncode"] : WanderScene.LEAVESTATE_FINISHED;

		return created;

	}

}
