import {WanderScene} from "../wander-scene";
import {WanderGame} from "../wander-game";
import {WorldMap} from "../world/world-map";
import {Place} from "../world/locs/Place";
import {Roaming} from "./roaming";
import {WorldPosition} from "../world/world-position";

export class JourneyDeparture  extends  WanderScene {

	worldmap: WorldMap;

	destination: Place;

	constructor (game: WanderGame)
	{
		super(game);
		this.worldmap = game.worldmap;
	}

	sceneFinished(): boolean {
		return !!this.destination;
	}

	setDestination (dest: Place)
	{
		// let startingpos = this.game.hero.coordinates;
		// let destcoords = dest["coords"];
		this.destination = dest;
	}

	getFollowingScene(): WanderScene
	{
		let next = null;
		if (!this.destination.matchesPosition(this.game.hero.pos))
		{
			let direction = WorldPosition.getRelativeGeneralDirection(this.game.hero.pos, this.destination.pos);
			next = new Roaming(this.game, this.game.hero.pos, direction);
			// next.setStops(this.game.hero.coordinates, this.destination["coords"]);
		}
		return next;
	}



}
