import {AfterViewInit, Component, OnInit} from '@angular/core';
import {StoryBranch} from "../../../rpg-engine/scenes/story-branch";
import {SceneRef} from "../../../scene-ui/scene-ref";
import {StorytellerInterface} from "../../storyteller-interface";
import {SceneRenderer} from "../scene-renderer";
import {SkillTest, SkillTestOption} from "../../../rpg-engine/scenes/skill-test";

@Component({
  selector: 'app-skill-action',
  templateUrl: './skill-action.component.html',
  styleUrls: ['./skill-action.component.scss']
})
export class SkillActionComponent extends SceneRenderer implements OnInit, AfterViewInit {

	skilltest: SkillTest;

	narr_fail: string;
	narr_done: string;

	constructor(public sceneref: SceneRef, storytellerinterface: StorytellerInterface = null) {

		super(sceneref, storytellerinterface);
		console.log("StoryChoice injected w stinterface: ", storytellerinterface);
		this.skilltest = this.sceneref.scene as SkillTest;
		console.log("current choice set is ", this.skilltest);
		this.narr_fail = null;
		this.narr_done = null;
	}

  ngOnInit() {
  }

	ngAfterViewInit(): void {
	}

	tryOption (option: SkillTestOption, succeed: boolean)
	{
		console.log("picked option ", option," for trying");
		let success = this.skilltest.playActionDebug(option, succeed);
		if (success)
		{
			this.narr_fail = null;
			this.narr_done = option.story_success;
		}
		else {
			this.narr_fail = option.story_failure;
		}


	}

	leaveScene ()
	{
		this.sceneref.triggerSceneCheck();
	}

}
