import {
	AfterViewInit, ChangeDetectorRef,
	Component,
	ComponentFactoryResolver,
	ComponentRef, EventEmitter,
	OnDestroy, Output,
	Type,
	ViewChild
} from '@angular/core';
import {Subject} from "rxjs";
import {InsertionDirective} from "./insertion.directive";
import { StorytellerComponent } from '../storyteller/storyteller.component';
import {CharSheetDirective} from "./char-sheet.directive";
import {QuestJournalComponent} from "../storyteller/charsheet/quest-journal/quest-journal.component";

/**
 * Holder component for {@link SceneRenderer} objects.
 * Is instantiated by {@link SceneUIService} for {@link StorytellerComponent} and injected with the chosen renderer.
 */
@Component({
	selector: 'app-scene-ui',
	templateUrl: './scene-ui.component.html',
	styleUrls: ['./scene-ui.component.scss']
})
export class SceneUIComponent implements AfterViewInit, OnDestroy {

	private readonly _onClose = new Subject<any>();

	public componentRef: ComponentRef<any>;
	public childComponentType: Type<any>;
	public onClose = this._onClose.asObservable();

	public controller: StorytellerComponent;

	showCharSheet: boolean;

	@ViewChild(InsertionDirective, {static: false}) insertionPoint: InsertionDirective;
	@ViewChild(CharSheetDirective, {static: false}) charsheetPoint: CharSheetDirective;

	constructor(private componentFactoryResolver: ComponentFactoryResolver, private cd: ChangeDetectorRef)
	{

		this.showCharSheet = false;
	}

	// LIFECYCLE

	// note: ngOnInit not strictly necessary for this use
	// ngOnInit() {
	// }

	ngAfterViewInit(): void {
		this.loadChildComponent(this.childComponentType);
		this.cd.detectChanges();
	}

	ngOnDestroy(): void {
		if (this.componentRef) {
			this.componentRef.destroy();
		}
	}

	// child loading

	loadChildComponent(componentType: Type<any>) {
		let componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentType);

		let viewContainerRef = this.insertionPoint.viewContainerRef;
		viewContainerRef.clear();

		this.componentRef = viewContainerRef.createComponent(componentFactory);
	}

	// charsheet loading, could probably be merged with loadChildComponent
	loadCharSheetComponent (componentType: Type<any>) {
		let componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentType);

		let viewContainerRef = this.charsheetPoint.viewContainerRef;
		viewContainerRef.clear();

		this.componentRef = viewContainerRef.createComponent(componentFactory);
	}

	// for dialog-like behaviour, revise

	onOverlayClicked(evt: MouseEvent) {
		console.warn("on overlay click");

	}

	onDialogClicked(evt: MouseEvent) {
		evt.stopPropagation();
	}


	doSaveGame ()
	{
		console.log("Saving game state... to outer Storyteller", this.controller);
		// WanderGameState.saveToBrowser(this.game);
		this.controller.doSaveGame();
	}

	doLoadGame ()
	{
		console.log("Loading game state... to outer Storyteller", this.controller);
		this.controller.doLoadGame();
	}

	openCharSheet ()
	{
		this.loadCharSheetComponent(QuestJournalComponent);
		this.controller.openCharSheet();
		console.log("opening char sheet");
		this.showCharSheet = true;

	}

	closeCharSheet ()
	{
		console.log("closing char sheet");
		this.controller.closeCharSheet();
		this.showCharSheet = false;
	}

}
