import {Place} from "./Place";
import {WorldPosition} from "../world-position";

export class Town extends Place
{

	constructor (locid: string, coords: WorldPosition)
	{
		super(coords);
		this.loctype = "towns";
		this.locid = locid;
	}


	static fromJSON (placeid: string, desc: object): Town
	{
		let coords = desc["coords"];
		let loaded = new Town (placeid, new WorldPosition(coords[0], coords[1]));
		loaded.name = desc["name"];

		return loaded;
	}
}
