import {WanderScene} from "../wander-scene";
import {MetaScene} from "./meta-scene";
import {WanderSceneTrigger} from "./struct/wander-scene-trigger";
import {WanderGame} from "../wander-game";

export class BranchingMetaScene extends MetaScene
{
	// scene triggers are more complex than in MetaScene; as such the key represents the TRIGGERED scene, not the triggerer
	// also, there can be only ONE trigger per scene, though it's free in terms of complexity as long as  it's derived from SceneTrigger
	triggers_byscene: Map<string, WanderSceneTrigger>;


	constructor (game: WanderGame)
	{
		super(game);
		this.initStructure();
	}

	initStructure ()
	{
		this.subs = new Map<string, WanderScene>();
		this.triggers_byscene = new Map<string, WanderSceneTrigger>();
		this.returncodes = new Map<string, number>();
		this.starter = null;
		this.activesub = null;
	}


	setTriggered (sceneid: string, trigger: WanderSceneTrigger)
	{
		this.triggers_byscene.set(sceneid, trigger);
	}

	moveOn ()
	{
		// NOTE: this method ASSUMES we have a sceneFinished call returning true right before

		console.log("Quest/BranchingScene: MetaScene " + this + " MoveOn from " + this.activesub);

		this.returncodes.set(this.activesub, this.getCurrent().returnState());

		// switches to the next scene in the flow
		// NOTE: it takes the FIRST active trigger, conflicts are not handled
		this.activesub = null;
		for (let candidate of this.triggers_byscene.keys())
		{
			let trigger = this.triggers_byscene.get(candidate);
			let candidatestate = this.returncodes.get(candidate);
			if (trigger.isActive(this.returncodes) && candidatestate === WanderScene.LEAVESTATE_UNFINISHED)
			{
				// we have triggered a new scene
				this.activesub = candidate;
			}
		}

		console.log("Quest/MetaScene", "MetaScene " + this + " MoveOn to " + this.activesub);
	}


}
