import {WanderScene} from "../wander-scene";
import {WanderGame} from "../wander-game";
import {TerrainPass} from "../world/locs/TerrainPass";
import {JourneyDeparture} from "./journey-departure";
import {WorldPosition} from "../world/world-position";

export class PassageTrail extends WanderScene
{

	/**
	 * The TerrainPass location being traversed.
	 * Since passes can generate TWO trails, back and forth, we extract the significant elemnents in the trail_* values with resetTrail() according to the current position of the player.
	 * Since we are outside the normal map fl,ow, the position of the player is updated only at the end of the current trail.
	 */
	passage: TerrainPass;

	// trail_* are the values generated from passage with direction applied
	trail_start: WorldPosition;
	trail_end: WorldPosition;
	trail_length: number;

	/**
	 * where the player is on the current trail
	 */
	progress: number;
	/**
	 * whether the player chose to leave AFTER reaching an end (for scenecheck)
	 */
	leaving: boolean;

	// tracker for total number of days spent in the passage?
	days: number;

	constructor (game: WanderGame, passage: TerrainPass, started: boolean = false)
	{
		super(game);
		this.passage = passage;
		// progress is the speed, negative allows to go back and reverse
		this.resetTrail(started);
		this.days = 0;
		this.leaving = false;
	}

	resetTrail (started: boolean = false)
	{
		const reversed = this.passage.exitpoint.matches(this.game.hero.pos);
		this.trail_start = reversed ? this.passage.exitpoint : this.passage.pos;
		this.trail_end = reversed ? this.passage.pos : this.passage.exitpoint;
		this.trail_length = this.passage.length();
		this.progress = started ? 0 : -1;
	}

	sceneFinished(): boolean
	{
		// boolean set by UI
		console.log("is at exit? " + this.isAtExitPoint() + "is leaving? " + this.leaving);
		return this.isAtExitPoint() && this.leaving;
	}

	getFollowingScene(): WanderScene {
		return new JourneyDeparture(this.game);
	}

	isAtExitPoint ()
	{
		return this.progress === -1 || this.progress === this.passage.length();
	}

	step ()
	{
		this.progress += 1;
		// this.progress += direction;
		this.progress = Math.min(Math.max(this.progress, -1), this.passage.length());
		if (this.progress === this.passage.length())
		{
			this.game.hero.setCoordinates(this.trail_end);
			this.resetTrail();
		}
	}



}
