import {WanderScene} from "../wander-scene";
import {WanderGame} from "../wander-game";
import {Town} from "../world/locs/Town";
import {Place} from "../world/locs/Place";
import {JourneyDeparture} from "./journey-departure";
import { QuestTask } from '../quests/quest-task';

export class TownNav extends WanderScene  {

	town: Town;
	locpoint: string;
	townstruct: object;

	static LOC_LEAVE = "OUT";
	static LOC_OUTSKIRTS = "LEAVE";

	currentloc: TownLocStructure;

	constructor (game: WanderGame, place?: Place)
	{
		super(game);
		this.town = place as Town;

		console.log(game.worldmeta);
		this.townstruct = game.worldmeta["townstructure"]["major"];
		this.locpoint = TownNav.LOC_OUTSKIRTS;
		this.updateLocation();
	}

	updateLocation ()
	{
		this.currentloc = this.getLocStructure();
		if (this.locpoint !== TownNav.LOC_LEAVE)
		{
			this.game.hero.locpos = {
				"locid": this.town.locid,
				"roomid": this.locpoint
			};
		}
		else
		{
			this.game.hero.locpos = null;
		}
	}

	getFollowingScene(): WanderScene {
		return new JourneyDeparture(this.game);
	}

	sceneFinished(): boolean {
		return this.locpoint === TownNav.LOC_LEAVE;
	}

	getLocStructure (): TownLocStructure
	{
		return this.townstruct["locs"][this.locpoint];
	}

	moveToLoc (locid: string)
	{
		this.locpoint = locid;
		this.updateLocation();
	}



}

export interface TownLocStructure
{
	locid: string;
	description: string;
	moves: {
		"target": string,
		"prompt": string
	}[];
	image: string;
}
