import {WorldPosition} from "./world/world-position";
import {AgentEntity} from "./skill-system/agent-entity";
import {SimpleSkillSet} from "./skill-system/simple-skill-set";

export class WanderHero implements AgentEntity
{

	pos: WorldPosition;
	locpos: object;
	skills: SimpleSkillSet;

	maxhealth: number;
	health: number;

	constructor ()
	{
		this.pos = null;
		this.locpos = null;
		this.skills = new SimpleSkillSet();
	}

	static fromJSON (src: object): WanderHero
	{
		let hero = new WanderHero();

		hero.setCoordinates(WorldPosition.fromCoordsArray(src["position"]["coords"]));

		for (let potential in src["record"]["potentials"])
		{
			let range = src["record"]["potentials"][potential];
			let value = Math.round(Math.random()*range[1]-range[0])+(range[1]-range[0]);
			hero.skills.registerPotential(potential, value);
		}

		for (let development in src["record"]["developments"])
		{
			let range = src["record"]["developments"][development];
			let value = Math.round(Math.random()*range[1]-range[0])+(range[1]-range[0]);
			hero.skills.registerDevelopment(development, value);
		}

		let range = src["record"]["health"];
		hero.maxhealth = Math.round(Math.random()*range[1]-range[0])+(range[1]-range[0]);
		hero.health = hero.maxhealth;

		return hero;

	}

	setCoordinates (pos: WorldPosition)
	{
		// console.log("replacing coords", this.pos, "w pos ", pos);
		this.pos = pos;
	}


	getSkills(): SimpleSkillSet {
		return this.skills;
	}

}
