import {Place} from "./Place";
import {WorldPosition} from "../world-position";

export class TerrainPass extends Place
{
	exitpoint: WorldPosition;
	course: string[];

	constructor(locid: string, entrypos: WorldPosition, exitpos: WorldPosition, course: string[]) {
		super(entrypos);
		this.loctype = "passes";
		this.locid = locid;
		this.exitpoint = exitpos;
		this.course = course;
	}

	length (): number
	{
		return this.course.length;
	}

	static fromJSON (placeid: string, desc: object): TerrainPass
	{
		let entrypoint = desc["entry"];
		let exitpoint = desc["exit"];
		let course = desc["sequence"];

		let loaded = new TerrainPass (placeid, new WorldPosition(entrypoint[0], entrypoint[1]), new WorldPosition(exitpoint[0], exitpoint[1]), course);
		loaded.name = desc["name"];

		return loaded;
	}

	matchesPosition(pos: WorldPosition): boolean {
		return this.pos.matches(pos) || this.exitpoint.matches(pos);
	}

}
