export class SimpleSkillThrow
{

	potentials: string[];
	developments: string[];


	constructor (req_pots: string[], req_devs: string[])
	{
		this.potentials = req_pots;
		this.developments = req_devs;
	}

	static fromJson(src: object): SimpleSkillThrow
	{
		return new SimpleSkillThrow(src["pot"], src["dev"]);
	}

}
