import {WorldPosition} from "../world-position";

export class Place
{

	pos: WorldPosition;

	locid: string;
	loctype: string;
	name: string;

	constructor (pos: WorldPosition)
	{
		this.pos = pos;
	}

	// public abstract getName (): string;

	public getCoords (): number[]
	{
		return this.pos.getCoords();
	}

	public matchesPosition (pos: WorldPosition): boolean
	{
		return this.pos.matches(pos);
	}


}

