import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {SceneRef} from "../../../scene-ui/scene-ref";
import {SceneRenderer} from "../scene-renderer";
import {TownNav} from "../../../rpg-engine/scenes/town-nav";
import {WalkRenderer} from "../lib/walk-renderer";
import {ArtRenderer} from "../lib/art-renderer";
import { QuestTask } from '../../../rpg-engine/quests/quest-task';
import {StorytellerComponent} from "../../storyteller.component";
import {StorytellerInterface} from "../../storyteller-interface";

@Component({
	selector: 'app-town-ui',
	templateUrl: './town-ui.component.html',
	styleUrls: ['./town-ui.component.scss']
})
export class TownUIComponent extends SceneRenderer implements OnInit, AfterViewInit {

	@ViewChild("locrender", {static: false}) locrender: ElementRef;

	viewrenderer: ArtRenderer;
	townscene: TownNav;

	taskprompts: QuestTask[];


	constructor(public sceneref: SceneRef, storytellerinterface: StorytellerInterface = null) {

		super(sceneref, storytellerinterface);
		console.log("TownNav injected w stinterface: ", storytellerinterface);
		this.townscene = this.sceneref.scene as TownNav;
		console.log("town info: ", JSON.stringify(this.townscene.town));
		this.taskprompts = [];
	}

	ngOnInit() {
	}

	ngAfterViewInit(): void {
		this.viewrenderer = new ArtRenderer(this.locrender);
		this.moveToLoc(this.townscene.locpoint);
	}

	moveToLoc (target: string)
	{
		this.townscene.moveToLoc(target);
		this.sceneref.triggerSceneCheck();
		this.renderView();
		this.updateTaskPrompts();
	}


	updateTaskPrompts ()
	{

		let quests = this.sceneref.game.quests.values();
		let qtrefs = [];
		for (let quest of quests)
		{
			// console.log("checking quest", quest);
			let available = quest.getAvailableTasks(this.sceneref.game);
			// console.log("found available tasks", available);
			let qtasks = [];
			for (let taskid of available)
			{
				// console.log("TASK AVAILABLE: ", taskid, "for quest", quest, "w state", quest.states.get(taskid));
				// this.st
				// prompts.push(quest.loadTask(taskid));
				qtrefs.push({
					quest: quest,
					taskid: taskid
				});
			}
		}

		this.taskprompts = [];
		let ui = this;
		this.stint.loadQuestTasks(qtrefs, function (tasks: QuestTask[])
		{
			let prompts = [];
			for (let qtask of tasks)
			{
				prompts.push(qtask);
			}

			// console.log("TownUI reaction to qtasks load", tasks," to prompts ", prompts);
			ui.taskprompts = prompts;

		});

	}


	renderView ()
	{
		let urlpath = this.townscene.currentloc.image;
		if (urlpath) {
			let fullpath = "./assets/graph/sceneviews/" + urlpath;
			this.viewrenderer.loadRenderImage(fullpath);
		}
	}

	localizeTextAsset (text: string): string
	{
		while (text.includes("$TOWN$"))
		{
			text = text.replace("$TOWN$", this.townscene.town.name);
		}
		return text;
	}



	activatePrompt (task: QuestTask)
	{
		console.log("prompt activation for task", task, "in", task.caller);

		task.scene.driver = task;
		// narrative should be set by StoryTeller
		this.sceneref.triggerSubScene(task.scene);


	}

}
