import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {WalkRenderer} from "../lib/walk-renderer";
import {Roaming} from "../../../rpg-engine/scenes/roaming";
import {SceneRef} from "../../../scene-ui/scene-ref";
import {SceneRenderer} from "../scene-renderer";

@Component({
	selector: 'app-roaming-step',
	templateUrl: './roaming-step.component.html',
	styleUrls: ['./roaming-step.component.scss']
})
export class RoamingStepComponent extends SceneRenderer implements OnInit, AfterViewInit {

	@ViewChild("renderholder", {static: false}) renderholder: ElementRef;
	@ViewChild("renderview", {static: false}) renderview: ElementRef;

	roamingscene: Roaming;
	viewrenderer: WalkRenderer;



	constructor(public sceneref: SceneRef) {
		super(sceneref);
		this.roamingscene = this.sceneref.scene as Roaming;
		console.log("roaming scene: ", this.roamingscene);

	}


	ngOnInit() {
	}


	ngAfterViewInit(): void {
		this.viewrenderer = new WalkRenderer(this.roamingscene.game.worldmap, this.renderview);
		let ui = this;
		this.viewrenderer.loadAssets(function ()
		{
			ui.renderStep();
		});
	}

	renderStep ()
	{
		const vector = this.roamingscene.direction;
		this.viewrenderer.renderView(this.roamingscene.current, vector);
	}


	getDirectionDescription (): string {
		let vector = this.roamingscene.direction;
		let dirstring = "";

		if (vector[1] > 0) {
			dirstring += "South";
		} else if (vector[1] < 0) {
			dirstring += "North";
		}
		if (vector[0] > 0) {
			dirstring += "East";
		} else if (vector[0] < 0) {
			dirstring += "West";
		}

		return dirstring;
	}
	getDayOfTravel ()
	{
		return this.roamingscene.steps + 1;
	}

	moveTurnLeft ()
	{
		this.roamingscene.changeDirection(-1);
		this.renderStep();
	}

	moveTurnRight ()
	{
		this.roamingscene.changeDirection(1);
		this.renderStep();
	}

	moveForward ()
	{
		this.roamingscene.stepForward();
		this.roamingscene.game.hero.setCoordinates(this.roamingscene.current);
		if (!this.roamingscene.sceneFinished())
		{
			this.renderStep();
		}
		else {
			this.sceneref.triggerSceneCheck();
		}
	}

	actionSetCamp ()
	{

	}


	showMap ()
	{
		// tba
	}


}
